.wrapper {
    align-self: stretch;
    display: grid;
    grid-template-rows: 30px 30px 1fr 30px;
    width: 100%;
}

.container {
    display: grid;
    justify-content: center;
    grid-template-columns: repeat(3, 30%);
    grid-auto-rows: calc(((100vw - 40px - 10px) * 3 / 10) + 90px);
    column-gap: 20px;
    row-gap: 20px;
    margin: 20px 0;
    width: 100%;
}

@media screen and (max-width: 700px) {
    .container {
        grid-template-columns: repeat(2, 45%);
        grid-auto-rows: calc(((100vw - 20px - 10px) * 45 / 100) + 120px);
    }
    .wrapper {
        grid-template-rows: 40px 40px 1fr 40px;
    }
}

@media screen and (min-width: 980px) {
    .container {
        grid-auto-rows: calc(((980px - 40px - 10px) * 3 / 10) + 90px);
    }
}

.boardLabel > span {
    margin: 0 5px;
}

.board {
    height: 100%;
    width: 100%;
    display: grid;
    grid-template-rows: repeat(8, 1fr);
    color: black;
    background-color: blue;
    animation: fade-in 0.5s ease;
}

@keyframes fade-in {
    from {
        opacity: 0%
    }
    to {
        opacity: 100%
    }
}

.row {
    display: grid;
    grid-template-columns: repeat(8, 1fr);
    color: black;
    background-color: blue;
}

.row:nth-child(odd) div:nth-child(even), .row:nth-child(even) div:nth-child(odd) {
    background-color: lightgreen;
}

.square {
    display: flex;
    align-items: center;
    justify-content: center;
}

.square.W {
    color: white
}

.square.B {
    color: black
}

.square > * {
    width: 61.8%;
    height: 61.8%;
}